/**
 * Vendor Fields js helper
 *
 * @package YITH WooCommerce Multi Vendor
 * @since 4.0.0
 */

import FieldsHandler from './fields-handler';
import AjaxRequest from "./ajax-request";

export default class VendorFieldsHandler extends FieldsHandler {

	states = null;

	init() {
		if ( typeof yith_wcmv_vendors !== 'undefined' ) {
			/* State/Country select boxes */
			this.states = JSON.parse(
				yith_wcmv_vendors.countries.replace( /&quot;/g, '"' )
			);
		}

		// Init special image upload value.
		this.container.find( '.vendor-image-upload-input' ).each( this.initImageValue.bind(this) );
		// Event listener.
		this.container.on( 'change', '.country-field', { self: this }, this.changeCountry );
		jQuery( '.country-field' ).change(); // init.
		// Handle image upload
		this.container.on( 'click', '.upload_vendor_image_button', { self: this }, this.uploadImage );
		this.container.on( 'click', '.remove_vendor_image_button', { self: this }, this.removeImage );

		super.init();

		this.container.on( 'keyup', '.ajax-check', {self: this}, this.validateField );
		this.initSlugField();
	}

	addImage( container, id, url ) {
		container.find( 'input[type="hidden"]' ).val( id );
		container.css( 'background-image', 'url(' + url + ')' );
		container.find( '.upload_vendor_image_button' ).hide();
		container.find( '.remove_vendor_image_button' ).show();
	}

	removeImage( event ) {
		event.preventDefault();

		let container = jQuery( this ).closest( '.vendor-image-upload' );

		container.find( 'input[type="hidden"]' ).val( '' );
		container.css( 'background-image', '' );
		container.find( '.upload_vendor_image_button' ).show();

		jQuery( this ).hide();

		return false;
	}

	changeCountry( event ) {
		const self = event.data.self;
		// Prevent if we don't have the data.
		if ( self.states === null ) {
			return;
		}

		let $this = jQuery( this ),
			country = $this.val(),
			$state = $this.closest( 'form' ).find( '.state-field' ),
			$parent = $state.parent(),
			stateValue = $state.val(),
			input_name = $state.attr( 'name' ),
			input_id = $state.attr( 'id' ),
			placeholder = $state.attr( 'placeholder' ),
			$newstate;

		// Remove the previous DOM element.
		$parent.show().find( '.select2-container' ).remove();

		if ( ! jQuery.isEmptyObject( self.states[country] ) ) {
			let state = self.states[country],
				$defaultOption = jQuery( '<option value=""></option>' ).text(
					yith_wcmv_vendors.i18nSelectStateText
				);

			$newstate = jQuery( '<select></select>' )
				.prop( 'id', input_id )
				.prop( 'name', input_name )
				.prop( 'placeholder', placeholder )
				.addClass( 'state-field' )
				.append( $defaultOption );

			jQuery.each( state, function( index ) {
				let $option = jQuery( '<option></option>' )
					.prop( 'value', index )
					.text( state[index] );
				if ( index === stateValue ) {
					$option.prop( 'selected' );
				}
				$newstate.append( $option );
			} );

			$newstate.val( stateValue );
			$state.replaceWith( $newstate );
			$newstate.show().selectWoo().hide().trigger( 'change' );
		} else {
			$newstate = jQuery( '<input type="text" />' )
				.prop( 'id', input_id )
				.prop( 'name', input_name )
				.prop( 'placeholder', placeholder )
				.addClass( 'state-field' )
				.val( stateValue );
			$state.replaceWith( $newstate );
		}
	}

	uploadImage( event ) {
		event.preventDefault();

		const self = event.data.self,
			container = jQuery( this ).closest( '.vendor-image-upload' );

		// Create the media frame.
		const file_frame = (wp.media.frames.downloadable_file = wp.media( {
			title: yith_wcmv_vendors.uploadFrameTitle,
			button: {
				text: yith_wcmv_vendors.uploadFrameButtonText,
			},
			multiple: false,
		} ));

		// When an image is selected, run a callback.
		file_frame.on( 'select', function() {
			const attachment = file_frame.state().get( 'selection' ).first().toJSON();
			self.addImage( container, attachment.id, attachment.sizes.full.url );
		} );

		// Finally, open the modal.
		file_frame.open();
	}

	initImageValue( index, input ) {
		let current = jQuery( input ),
			value = current.data( 'value' ) ?? null;

		if ( null === value ) {
			return;
		}
		for ( var key in value ) {
			this.addImage( current.parent(), key, value[key] );
		}

		current.removeAttr( 'data-value' );
	}

	initSlugField() {
		let slug = this.container.find( 'input#slug' ),
			desc = slug.closest( '.vendor-field' ).find( '.description' );

		desc.data( 'text', desc.text() );
		slug.on( 'keyup', function() {
			let val = jQuery( this ).val();
			if ( val ) {
				val = val.toLowerCase().replace( /[^0-9a-z-]+/, '-' );
				desc.text( desc.data('text').replace( '%yith_shop_vendor%', val ) );
				jQuery( this ).val( val );
			} else {
				desc.text('');
			}
		} ).keyup();
	};


	validateField( event ) {

		let self = event.data.self;
		// Call must be unique. Abort the current one if processing
		AjaxRequest.abort();

		let input = jQuery( this ),
			value = input.val();

		// Reset field.
		input.removeClass( 'error success' );
		if ( ! value.length ) {
			return false;
		}

		// Add loading icon.
		input.addClass( 'loading' );
		AjaxRequest.get(
			{
				request: input.data('action'),
				value: value,
				vendor_id: self.container.find('input[name="vendor_id"]').val()
			} )
			.fail( () => {
				input.removeClass( 'loading' );
			} )
			.done( ( res ) => {
				input.removeClass( 'loading' );

				if ( res.success ) {
					input.addClass( 'success' );
					self.resetFieldError( input.attr('name') );
				} else {
					input.addClass( 'error' );
					self.addFieldError( res.data.error, input.attr('name') );
				}
			} );
	}
}
